import { useEffect } from 'react'
import { Routes, Route, Navigate } from 'react-router-dom'
import { Layout } from './components/Layout'
import { Dashboard } from './pages/Dashboard'
import { Threats } from './pages/Threats'
import { ThreatDetail } from './pages/ThreatDetail'
import { Policies } from './pages/Policies'
import { Compliance } from './pages/Compliance'
import { Settings } from './pages/Settings'
import { useAuthStore } from './store/authStore'
import { appConfig } from './config/runtime'

function App() {
  const { isAuthenticated } = useAuthStore()
  const demoEnabled = appConfig.demoAuth

  const branch = !isAuthenticated && !demoEnabled ? 'sign-in-block' : 'app'
  // #region agent log
  fetch('http://127.0.0.1:7242/ingest/35412ca6-bb7d-42f7-a953-14c18ef820ca', { method: 'POST', headers: { 'Content-Type': 'application/json' }, body: JSON.stringify({ location: 'App.tsx:render', message: 'App render', data: { isAuthenticated, demoEnabled, branch }, timestamp: Date.now(), sessionId: 'debug-session', hypothesisId: 'H1' }) }).catch(() => {});
  fetch('http://127.0.0.1:7242/ingest/35412ca6-bb7d-42f7-a953-14c18ef820ca', { method: 'POST', headers: { 'Content-Type': 'application/json' }, body: JSON.stringify({ location: 'App.tsx:render', message: 'App render H2/H3', data: { isAuthenticated, demoEnabled, branch, showingAppBeforeAuth: branch === 'app' && !isAuthenticated }, timestamp: Date.now(), sessionId: 'debug-session', hypothesisId: 'H2' }) }).catch(() => {});
  // #endregion

  useEffect(() => {
    // #region agent log
    fetch('http://127.0.0.1:7242/ingest/35412ca6-bb7d-42f7-a953-14c18ef820ca', { method: 'POST', headers: { 'Content-Type': 'application/json' }, body: JSON.stringify({ location: 'App.tsx:useEffect', message: 'useEffect demo-login entry', data: { isAuthenticated, demoEnabled, willCallLogin: !isAuthenticated && !!demoEnabled }, timestamp: Date.now(), sessionId: 'debug-session', hypothesisId: 'H3' }) }).catch(() => {});
    // #endregion
    if (!isAuthenticated && demoEnabled) {
      // #region agent log
      fetch('http://127.0.0.1:7242/ingest/35412ca6-bb7d-42f7-a953-14c18ef820ca', { method: 'POST', headers: { 'Content-Type': 'application/json' }, body: JSON.stringify({ location: 'App.tsx:useEffect', message: 'before demo login call', data: {}, timestamp: Date.now(), sessionId: 'debug-session', hypothesisId: 'H4' }) }).catch(() => {});
      // #endregion
      const p = useAuthStore.getState().login('demo', 'demo-token')
      p.then(() => {
        // #region agent log
        fetch('http://127.0.0.1:7242/ingest/35412ca6-bb7d-42f7-a953-14c18ef820ca', { method: 'POST', headers: { 'Content-Type': 'application/json' }, body: JSON.stringify({ location: 'App.tsx:useEffect', message: 'demo login success', data: {}, timestamp: Date.now(), sessionId: 'debug-session', hypothesisId: 'H4' }) }).catch(() => {});
        // #endregion
      }).catch((err) => {
        // #region agent log
        fetch('http://127.0.0.1:7242/ingest/35412ca6-bb7d-42f7-a953-14c18ef820ca', { method: 'POST', headers: { 'Content-Type': 'application/json' }, body: JSON.stringify({ location: 'App.tsx:useEffect', message: 'demo login failed', data: { err: String(err?.message || err) }, timestamp: Date.now(), sessionId: 'debug-session', hypothesisId: 'H4' }) }).catch(() => {});
        // #endregion
      })
    }
  }, [demoEnabled, isAuthenticated])

  if (!isAuthenticated && !demoEnabled) {
    return (
      <div className="min-h-screen bg-slate-950 text-slate-100 flex items-center justify-center p-6">
        <div className="card max-w-md w-full p-6 space-y-4">
          <div>
            <h1 className="text-xl font-semibold">Sign-in required</h1>
            <p className="text-sm text-slate-400 mt-2">
              Demo access is disabled for this environment. Configure authentication or enable
              demo mode to continue.
            </p>
          </div>
          <div className="rounded-lg border border-slate-800/80 bg-slate-900/60 px-4 py-3 text-sm text-slate-300">
            <p className="font-medium text-slate-200">Next steps</p>
            <p className="mt-1">
              Set <span className="font-mono">VITE_DEMO_AUTH=true</span> for local demos, or
              connect your SSO provider and auth service.
            </p>
          </div>
          <p className="text-xs text-slate-500">
            Need help? Contact {appConfig.supportEmail}.
          </p>
        </div>
      </div>
    )
  }

  return (
    <Layout>
      <Routes>
        <Route path="/" element={<Dashboard />} />
        <Route path="/threats" element={<Threats />} />
        <Route path="/threats/:id" element={<ThreatDetail />} />
        <Route path="/policies" element={<Policies />} />
        <Route path="/compliance" element={<Compliance />} />
        <Route path="/settings" element={<Settings />} />
        <Route path="*" element={<Navigate to="/" replace />} />
      </Routes>
    </Layout>
  )
}

export default App
